.has-fade {
    visibility: hidden;
}

@keyframes fade-in {
    from {
        visibility: hidden;
        opacity: 0;
    }
    1% {
        visibility: visible;
        opacity: 0;
    }
    to {
        visibility: visible;
        opacity: 1;
    }
}

.fade-in {
    animation: fade-in .3s ease-in-out forwards;
}

@keyframes fade-out {
    from {
        visibility: visible;
        opacity: 1;
    }
    99% {
        visibility: visible;
        opacity: 0;
    }
    to {
        visibility: hidden;
        opacity: 0;
    }
}

.fade-out {
    animation: fade-out .3s ease-in-out forwards;
}

@keyframes slider-content-in {
    from {
      opacity: 0;
      transform: translateY(100%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  .slider .slider-content div.active h4 {
    animation: slider-content-in .4s ease-in-out forwards;
  }
  .slider .slider-content div.active p {
    animation: slider-content-in .4s ease-in-out forwards;
  }
  
  @keyframes slider-content-out {
    from {
      opacity: 1;
      transform: translateY(0);
    }
    to {
      opacity: 0;
      transform: translateY(-100%);
    }
  }
  .slider .slider-content div.inactive h4 {
    animation: slider-content-out .4s ease-in-out forwards;
  }
  .slider .slider-content div.inactive p {
    animation: slider-content-out .4s ease-in-out forwards;
  }
  
  @keyframes slider-show {
    from {
      opacity: 0;
      transform: scale(0);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  .slider .slider-content div.active .slider-img img {
    animation: slider-show .5s ease-in-out forwards;
  }
  
  @keyframes slider-hide {
    from {
      opacity: 1;
      transform: scale(1);
    }
    to {
      opacity: 0;
      transform: scale(0);
    }
  }
  .slider .slider-content div.inactive .slider-img img {
    animation: slider-hide .5s ease-in-out forwards;
  }