html {
    box-sizing: border-box;
    font-size: 100%;
}
*,*::before,*::after {
    box-sizing: inherit;
}
body {
    margin: 0;
    padding: 0;
    background: $mainBackground;
    overflow-x: hidden;
  
    @include breakpoint-up(large) {
      font-size: $fontMd;
    }
  }
a, a:visited, a:hover {
    text-decoration: none;
}
li {
  list-style-type: none;
}
h1 {
  font-size: 2.5rem;
  @include breakpoint-up(large) {
    font-size: $fontXlg;
  }
}
h2 {
  font-size: $fontLg;
  @include breakpoint-up(large) {
    font-size: 2.5rem;
  }
}
h3 {
  font-size: 1.5rem;
  @include breakpoint-up(large) {
    font-size: $fontLg;
  }
}
.container {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 1rem;
  max-width: 68.75rem;
  margin: 0 auto;
  @include breakpoint-up(medium) {
    grid-template-columns: repeat(12, 1fr  );
  }
}
.hide-for-mobile {
  @include breakpoint-down(small) {
    display: none;
  }
}
.hide-for-desktop {
  @include breakpoint-up(medium) {
    display: none;
  }
}
.main-button {
  padding: .3rem 1.5rem;
  background: $btnColor;
  border-radius: .5rem;
  font-size: $fontMd;
  font-family: eczar-bold;
  text-transform: uppercase;
  color: $white;
  text-shadow: $textShadow;
  box-shadow: $boxShadow;
  z-index: 10;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  transition: all .3s ease-in-out;

  &:hover {
    color: $mainYellow;
    background: $btnHover;
  }

  &::after {
    content: "";
    position: absolute;
    background: $btnColor;
    height: 1.5rem;
    left: 20%;
    top: 30%;
    transform-origin: bottom left;
    transition: transform .2s ease-in-out;
  }

  &:hover::after, &:focus::after {
    transform: scale(1.2, 1.5);
    background: $btnHover;
    box-shadow: $boxShadow;
  }
}