header {

.header-logo {
    line-height: 1.3;

    @include breakpoint-up(large) {
        line-height: 1.6;
    }

    & h4 {
    font-family: eczar-regular;
    font-size: $fontMd;
    color: $white;
    font-style: italic;
    text-transform: capitalize;
    text-shadow: $textShadow;

    @include breakpoint-up(large) {
        font-size: 1.2rem;
    }

        & .header-clock {
        font-family: eczar-semibold;
        font-style: normal;
        color: $mainYellow;
        text-align: center;

            @include breakpoint-up(large) {
                color: $white;
                text-align: left;
            }
        }
    }
}

    nav {
        grid-column: 1/-1;
        padding: 0 1rem;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;

        & .menu-btn {
            position: absolute;
            height: 3rem;
            width: 2.5rem;
            right: 0;
            top: 7%;
            z-index: 10;
            transform: rotate(0deg);
            cursor: pointer;
            transition: all .7s ease-in-out;
            position: relative;

            & span {
                display: block;
                position: absolute;
                height: .5rem;
                width: 100%;
                background: $mainYellow;
                box-shadow: .1rem .1rem .1rem $mainBlack;
                opacity: 1;
                left: 0;
                border-radius: 1rem;
                z-index: 10;
                transform: rotate(0deg);
                cursor: pointer;
                transition: all .7s ease-in-out;
                border-bottom: .1875rem solid transparent;

                &:nth-child(1) {
                    top: 0;
                }
                &:nth-child(2) {
                    top: .6875rem;
                }
                &:nth-child(3) {
                    top: 1.375rem;
                }
            }

            &.open span:nth-child(1) {
                top: 1.125rem;
                transform: rotate(135deg);
            }
            &.open span:nth-child(2) {
                opacity: 0;
                left: -3.75rem;
            }
            &.open span:nth-child(3) {
                top: 1.125rem;
                transform: rotate(-135deg);
            }
        }

        .search-form {
            position: absolute;
            right: 4.2rem;
            top: .8rem;
            background: $mainBackground;
            background-image: url(/dist/images/search-icon-white.png);
            background-repeat: no-repeat;
            background-position-y: 45%;
            background-size: auto;
            transition: all .3s ease-in-out;

            @include breakpoint-up(large) {
                top: 1.7rem;
            }

            &.close {
                opacity: 0;
            }

            & label, input[type="submit"] {
                display: none;
            }

            & input[type="search"] {
                background: transparent;
                outline: none;
                border: hidden;
                color: $white;
                height: 3rem;
                width: 1.8rem;
                text-indent: 1.8rem;
                transition: all .4s ease-in-out;
                cursor: pointer;

                &::placeholder {
                    color: $white;
                    font-size: $fontMd;
                    font-family: eczar-bold;
                    letter-spacing: 1px;
                }

                &:focus {
                    width: 80vw;
                    transition: width .8s ease-in-out;
                    background: $mainBackground;
                    border-radius: 1rem;

                    @include breakpoint-up(medium) {
                        width: 35rem;
                        border: 1px solid $white;
                    }
                }
            }
        }

        .desktop-links {
            margin-right: 8rem;

            & a {
                font-size: $fontMd;
                font-family: eczar-bold;
                color: $mainYellow;
                text-transform: uppercase;
                text-shadow: $textShadow;
                position: relative;

                &:not(:last-child) {
                    margin-right: 1rem;
                }

                &::before {
                    content: "";
                    position: absolute;
                    display: block;
                    height: .2rem;
                    left: 0;
                    right: 0;
                    bottom: -.5rem;
                    background: #e43a15;
                    transform: scale(0, 1);
                    transition: transform .3s ease-in-out;
                }

                &:hover::before {
                    transform: scale(1.2, 1);
                }
            }
        }
    }

    .mobile-links {
        position: absolute;
        width: 100%;
        height: 55%;
        left: 50%;
        transform: translateX(-50%);
        background-color: $mainBackground;
        padding: 0 2rem;
        z-index: 100;

        & a {
            display: block;
            font-family: eczar-bold;
            font-size: 1.5rem;
            color: $mainYellow;
            text-align: center;
            text-transform: uppercase;
            margin-bottom: 1.2rem;
            text-shadow: $textShadow;
            position: relative;

            &::before {
                content: "";
                position: absolute;
                display: block;
                height: .3125rem;
                left: 35%;
                right: 35%;
                bottom: -.3rem;
                background: #e43a15;
                transform: scale(0, 1);
                transition: transform .5s ease-in-out;
            }

            &:hover::before {
                transform: scale(1.2, 1);
            }
        }
    }
}