.short-stories {
    margin: 1rem;
    padding: 1rem;
    background: linear-gradient(
      rgba($mainBlack, .4), 
      rgba($mainBlack, .4)), 
      url(/dist/images/books.jpg) center/cover no-repeat;
  
    & .section-title {
        font-size: 1.7rem;
        line-height: 1;
        color: $mainYellow;
        font-family: eczar-regular;
        text-align: center;
        text-shadow: $textShadow;
        text-transform: capitalize;

        @include breakpoint-up(medium) {
            font-size: $fontLg;
        }
    }
   
    & .main-content {
      grid-column: 1/-1;
      align-self: start;
  
      @include breakpoint-up(medium) {
        grid-column: 1/8;
      }
  
      .card {
        background: $white;
        box-shadow: $boxShadow;
  
        &-header {
          background: $mainBackground;
          padding: 0.5rem;
          font-family: eczar-bold;
          color: $white;
          text-transform: capitalize;
          text-shadow: $textShadow;
          box-shadow: $boxShadow;
          text-align: center;
        }
  
        & .card-body {
          padding: 1rem;
  
          & .display-box {
            width: 100%;
            font-family: eczar-regular;
            font-size: $fontMd;
            color: $mainBlack;
          }
  
          & .scroll-box {
            overflow-y: auto;
            height: 40vh;
  
            @include breakpoint-up(medium) {
              height: 100vh;
            }
          }
        }
  
        &-footer {
          padding: 0.7rem;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
          align-items: center;
          box-shadow: $boxShadow;
  
          & .fa {
            color: $btnColor;
            font-size: $fontLg;
            text-shadow: $textShadow;
            transition: transform 0.3s ease, color 0.3s ease;
  
            &:hover {
              transform: translateY(-20%);
              color: $mainYellow;
            }
          }
        }
      }
    }
  
    & .sidebar-widgets {
      grid-column: 1/-1;
  
      @include breakpoint-up(medium) {
        grid-column: 9/-1;
      }
  
      & .card {
        background: $white;
        margin: 1rem 0;
        max-height: 70%;
        box-shadow:$boxShadow;
  
        @include breakpoint-up(medium) {
          margin-top: 0;
          margin-bottom: 1rem;
        }
  
        &-header {
          padding: 0.5rem;
          background: $mainBackground;
          color: $white;
          font-family: eczar-bold;
          font-size: $fontMd;
          text-transform: uppercase;
          text-align: center;
          text-shadow: $textShadow;
          box-shadow: $boxShadow;
        }
  
        &-body {
          padding-bottom: 0.5rem;
          width: 100%;
        }
      }
  
      & .select-story ul li a {
        font-size: $fontMd;
        line-height: 2;
  
        @include breakpoint-up(large) {
          line-height: 1.3;
        }
      }
  
      & .search-form {
        & .card-body {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-wrap: wrap;
          padding-bottom: 1rem;
  
          & input,
          .search-btn {
            padding: 0.5rem;
            outline: 0;
            margin-bottom: 0.3rem;
          }
  
          & .search-btn {
            margin-left: 0.2rem;
            background: $btnColor;
            color: $white;
            font-weight: bold;
            border: 0.1rem solid $btnColor;
            box-shadow: $boxShadow;
            cursor: pointer;
  
            &:hover {
              background: $btnHover;
              color: $mainBlack;
            }
          }
        }
  
        & .card-footer {
          text-align: center;
          color: $mainRed;
          text-transform: uppercase;
          font-weight: bold;
          text-shadow: $textShadow;
          padding-bottom: 0.5rem;
        }
      }
  
      & .doc-count .card-body li {
        line-height: 1.2;
      }
    }
  }
  