.budget-calculator {
 
    & h2 {
      font-family: eczar-bold;
      color: $white;
      text-shadow: $textShadow;
      text-align: center;
      margin: 0;
    }
  
    & .budget-center {
      grid-column: 1/-1;
      align-items: start;
      margin: 0 1rem;
      padding: 1rem;
      background: 
      linear-gradient(
          rgba($mainBackground, 0.5),
          rgba($mainBackground, 0.5)
        ),
        url(/dist/images/budget4.jpg) center/cover no-repeat;
      box-shadow: $boxShadow;
      border-radius: 0.625rem;
  
      @include breakpoint-up(large) {
        margin: 0;
      }
  
      & .budget-feedback,
      & .expense-feedback {
        margin: 0;
        text-align: center;
        color: $white;
        font-size: $fontMd;
        font-family: eczar-regular;
        text-transform: capitalize;
        text-shadow: $textShadow;
        box-shadow: $boxShadow;
        background: $mainRed;
        display: none;
  
        @include breakpoint-up(large) {
          text-transform: uppercase;
        }
      }
  
      & .budget-feedback.showItem,
      & .expense-feedback.showItem {
        display: block;
      }
  
      & label h4 {
        font-family: eczar-bold;
        color: $white;
        text-shadow: $textShadow;
        text-transform: capitalize;
        padding-left: 0.5rem;
      }
  
      & .first-row {
        margin: 1rem 0;
  
        @include breakpoint-up(medium) {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-gap: 2rem;
          align-items: start;
        }
  
        & .budget-container {
          width: 100%;
          padding: 1rem;
          border: 1px solid $mainBlack;
          border-radius: 0.625rem;
          box-shadow: $boxShadow;
          text-align: center;
  
          & .budget-input {
            padding: 0.5rem 1rem;
            width: 100%;
            max-width: 60vh;
            border: 1px solid $mainBlack;
            border-radius: 0.625rem;
            outline: none;
            box-shadow: $boxShadow;
            margin-bottom: 1rem;
            font-weight: 900;
          }
  
          & .budget-submit {
            padding: 0.3rem;
            background: $btnColor;
            outline: 0;
            box-shadow: $boxShadow;
            font-family: eczar-bold;
            color: $white;
            text-shadow: $textShadow;
            cursor: pointer;
            transition: all 0.3s ease;
  
            &:hover {
              background: $btnHover;
              color: $mainBlack;
            }
          }
        }
  
        & .budget-info {
          display: flex;
          justify-content: space-around;
  
          & > div {
            text-align: center;
  
            & .info-title {
              font-family: eczar-bold;
              color: $white;
              text-shadow: $textShadow;
              text-transform: uppercase;
            }
  
            & .budget-icon,
            .expense-icon,
            .balance-icon {
              font-size: $fontLg;
              color: $btnColor;
              text-shadow: $textShadow;
            }
  
            & .budget,
            .expense,
            .balance {
              padding: 0 .5rem;
              background: rgba(0, 0, 0, 0.5);
              font-family: eczar-regular;
              font-size: 1.5rem;
              text-shadow: $textShadow;
              box-shadow: $boxShadow;
  
              @include breakpoint-up(large) {
                font-size: $fontLg;
              }
            }
  
            & .budget,
            .balance {
              color: $mainGreen;
            }
  
            & .expense {
              color: $mainRed;
            }
  
            & .balance {
              &.showRed {
                color: $mainRed;
              }
              &.showGreen {
                color: $mainGreen;
              }
              &.showBlack {
                color: $darkGray;
              }
            }
          }
        }
      }
  
      & .second-row {
        margin: 1rem 0;
  
        @include breakpoint-up(medium) {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-gap: 2rem;
          align-items: start;
        }
  
        & .expense-container {
          width: 100%;
          padding: 1rem;
          border: 1px solid $mainBlack;
          border-radius: 0.625rem;
          box-shadow: $boxShadow;
          text-align: center;
  
          & .expense-input {
            padding: 0.5rem 1rem;
            width: 100%;
            max-width: 60vh;
            margin: 0 auto;
            border: 1px solid $mainBlack;
            border-radius: 0.625rem;
            box-shadow: $boxShadow;
            margin-bottom: 1rem;
            font-weight: 900;
          }
  
          & .expense-submit {
            padding: 0.3rem;
            background: $btnColor;
            outline: 0;
            box-shadow: $boxShadow;
            font-family: eczar-bold;
            color: $white;
            text-shadow: $textShadow;
            cursor: pointer;
            transition: all 0.3s ease;
  
            &:hover {
              background: $btnHover;
              color: $mainBlack;
            }
          }
        }
  
        & .expense-list-info {
          padding: 0 1rem;
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
  
          & .list-item {
            font-family: eczar-bold;
            font-size: $fontMd;
            color: $white;
            text-transform: capitalize;
            text-shadow: $textShadow;
          }
        }
       
        & .expense {
          margin-top: -1rem;
          background: rgba(0, 0, 0, 0.5);
          box-shadow: $boxShadow;
  
          & .expense-item {
            padding: 0 1rem;
            display: flex;
            justify-content: space-between;
            align-items: baseline;
    
            & .expense-title,
            .expense-amount {
              color: $white;
              font-family: eczar-bold;
              font-size: $fontSm;
              text-align: center;
              text-transform: uppercase;
              text-shadow: $textShadow;
  
              @include breakpoint-up(large) {
                font-size: $fontMd;
              }
            }
  
            & .expense-amount {
              font-size: $fontMd;
              font-family: eczar-bold;
            }
  
            & .expense-icons {
              & a i {
                padding: 0 .5rem;
                text-shadow: $textShadow;
                cursor: pointer;
              }
  
              & .edit-icon {
                font-size: $fontMd;
                &:hover {
                  color: $btnColor;
                }
              }
  
              & .delete-icon {
                color: $mainRed;
                &:hover {
                  color: $btnColor;
                }
              }
            }
          }
        }
      }
    }
  }
  