.subscribe {
    margin: 3rem 0;
    height: 100%;
    position: relative;

    & .subscribe-form {
        position: absolute;
        top: 50%;
        left: 50%;
        text-align: center;
        transform: translate(-50%, -20%);
        width: 80vw;
        box-shadow: $boxShadow;
        border-radius: .5rem;
        background: rgb(122, 40, 40);
        padding-bottom: 2rem;

        &.sendEmail {
            background: url(/dist/images/nio-smith.jpg) center/cover no-repeat;
            border: .2rem double $white;
            
        }

        @include breakpoint-up (medium) {
            grid-column: 1/-1;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 1rem;
            justify-items: center;
            align-items: center;
            align-content: space-evenly;
            max-height: 14rem;
            transform: translate(-50%, -25%);
            background-size: 50%;
        }

        @include breakpoint-up(large) {
            width: 60vw;
        }

        & form {

            & #form-title {
                font-size: $fontMd;
                font-family: eczar-bold;
                text-transform: uppercase;
                color: $white;
                text-shadow: $textShadow;

                &.sendEmail {
                    opacity: 0;
                }

                @include breakpoint-up (medium) {
                    font-size: 1.5rem;
                }
            }

            & input[type="email"] {
                border-radius: 5rem;
                border: .1rem solid $white;
                outline: none;
                width: 90%;
                height: auto;
                min-height: 3rem;
                padding-left: 1rem;
                background: #7a2828;
                color: $white;
                
                &::placeholder {
                    color: $white;
                }

                &.sendEmail {
                    opacity: 0;
                }
            }

            & p {
                font-size: $fontSm;
                font-family: eczar-regular;
                color: $mainYellow;
                text-shadow:$textShadow;

                &.sendEmail {
                    opacity: 0;
                }
            }
        }

        & .subscribe-btn {
            padding: .4rem 1.5rem;
            background: $btnColor;
            border-radius: 5rem;
            font-family: eczar-regular;
            color: $white;
            text-shadow: $textShadow;
            box-shadow: $boxShadow;

            &.sendEmail {
                opacity: 0;
            }

            @include breakpoint-up(medium) {
               margin-top: 3.5rem;
               padding: .4rem 2.5rem;
            }
        }
    }
}