.banner {
    padding: 0 1rem;

    & .banner-container {
        padding: 1rem;
        grid-column: 1/-1;
        background: linear-gradient(
            rgba($mainBackground, .5),
            rgba($mainBackground, .5)),
            url(/dist/images/heavy-metal3.jpg) top center/cover no-repeat;
        border-radius: .5rem;
        height: auto;
        box-shadow: $boxShadow;

        @include breakpoint-up(medium) {
            padding-left: 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        & .banner-text {
            height: 50vh;
            width: 90%;

            @media screen and (min-width: 31.25rem) {
                padding-top: 5rem;
                background-image: url(/dist/images/lemmy2.png);
                background-size: 60%;
                background-repeat: no-repeat;
                background-position-x: -10%;
                background-blend-mode: screen;
            }

            @include breakpoint-up(medium) {
                height: 65vh;
                background-size: 80%;
            }

            @include breakpoint-up(large) {
                height: 70vh;
                background-size: 90%;
                padding-left: 2rem;
            }

            & h1 {
                width: 100%;
                margin: 0;
                font-family: eczar-bold;
                font-size: $fontLg;
                color: $white;
                text-shadow: $textShadow;
                line-height: 1.2;

                @media screen and (min-width: 31.25rem) {
                    padding-top: 5%;
                }

                @include breakpoint-up(medium) {
                    padding-top: 15%;
                }
            }

            & p {
                color: $mainYellow;
                font-family: eczar-bold;
                font-size: $fontSm;
                line-height: 1.5;
                text-shadow: $textShadow;

                @media screen and (min-width: 31.25rem) {
                    line-height: 1.3;
                }

                @include breakpoint-up(medium) {
                    line-height: 1.5;
                }
            }
        }

        .banner-image {
            height: 50vh;
            width: 100%;
            background-image: url(/dist/images/motorhead-band.png);
            background-size: 100%;
            background-repeat: no-repeat;
            background-position: top;
            transition: transform .5s ease-in-out;
            transition-delay: transform .5s ease-in-out;
            cursor: pointer;

            &:hover {
                transform: skew(-2deg, 5deg) scale(1.1);
            }

            @media screen and (min-width: 25rem) {
                height: 45vh;
                background-size: 45vh;
            }

            @include breakpoint-up(medium) {
                margin-top: 1rem;
                height: 60vh;
                background-size: 55vh;
            }

            @include breakpoint-up(large) {
                height: 65vh;
                background-size: 65vh;
            }
        }
    }
}