.footer {
    margin-top: 3rem;
    height: 100%;
    padding-top: 9rem;
    background: url(/dist/images/footer-bcg.jpg) top/cover no-repeat;

    & p, a, h4 {
        color: $white;
        font-family: eczar-regular;
        font-size: $fontMd;
        text-shadow: $textShadow;
        z-index: 5;
    }

    & .footer-wrapper {
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-gap: 1rem;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 1rem;

        & .footer-logo {
            line-height: .8;
            
            & h4 {
                font-size: $fontMd;
                font-style: italic;
                font-weight: 700;
                text-transform: capitalize;
            }

            & .footer-clock {
                display: inline-block;
                font-family: eczar-bold;
                color: $mainYellow;
                font-size: $fontMd;
            }

            @include breakpoint-up(medium) {
                grid-area: logo;
                text-align: left;
                margin-bottom: -2rem;
            }
         }

        & p {
            padding: 0 2.5rem;
            line-height: 1.4;
            align-self: start;

            @include breakpoint-up(medium) {
                grid-area: para;
                width: 80%;
                padding: 0;
                text-align: center;
                align-self: start;
            }
        }

        & .footer-contact {
            width: 100%;
            text-align: center;
            margin-bottom: -1rem;

            & a {
                display: flex;
                justify-content: center;
                align-items: center;
                padding-bottom: 1rem;

                &:hover {
                    color: $mainYellow;
                }

                & i {
                    padding-right: 1rem;
                    font-size: $fontLg;
                }
            }

            @include breakpoint-up(medium) {
                grid-area: contact;
                display: flex;
                justify-content: space-around;
            }
        }

        & .footer-links1, .footer-links2 {
            display: flex;
            flex-direction: column;
            padding-bottom: .5rem;

            & h4 {
                font-family: eczar-bold;
                text-transform: capitalize;
                color: $mainYellow;
                margin-bottom: .5rem;

                @include breakpoint-up(large) {
                    color: $white;
                }
            }

            & a {
                padding-bottom: 1rem;
                font-family: eczar-bold;
                font-size: .7rem;
                text-transform: uppercase;
                color: $mainYellow;

                @include breakpoint-up(large) {
                    font-size: $fontMd;
                }
            }
        }

        & .footer-links1 {
            @include breakpoint-up(medium) {
                grid-area: links1;
            }
        }

        & .footer-links2 {
            @include breakpoint-up(medium) {
                grid-area: links2;
            }
        }

        @include breakpoint-up(medium) {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: auto;
            grid-template-areas: 
            "logo links1 links2"
            "para links1 links2"
            "contact contact contact";
            grid-column-gap: 1rem;
            justify-items: center;
            align-items: start;
        }
    }

    .copyright {
        grid-column: 1/-1;
        padding: 1rem;
        width: 100%;
        height: auto;
        background: $mainBackground;
        text-align: center;
    
        & p {
            font-family: eczar-regular;
            font-size: $fontMd;
            color: $mainYellow;
        }
    }
}

