.shop {
    padding: 1rem;
    position: relative;

    & .container {
        background: url(/dist/images/rattan-bcg.jpg) center/cover fixed no-repeat;
        box-shadow: $boxShadow;
    }

    .shop-header {
        padding: 0 1rem;
        grid-column: 1/-1;
        display: flex;
        align-items: center;
        justify-content: space-around;  
      
        & h3 {
            color: $white;
            font-family: eczar-bold;
            text-transform: uppercase;
            text-shadow: $textShadow;
            font-style: italic;
        }

        & .cart-btn {
            color: $mainGreen;
            text-shadow: $textShadow;
            cursor: pointer;
            position: relative;

            & .cart-icon {
                font-size: 3rem;
            }

            & .cart-items {
                position: absolute;
                top: -1rem;
                left: 2.3rem;
                color: $mainYellow;
                font-size: 2rem;
                font-weight: bold;
                text-shadow: $textShadow;
            }
        }
    }
  
    & .products {
        grid-column: 1/-1;
        padding: 0 1rem;
        text-align: center;
        margin-top: -2rem;

        @include breakpoint-up(large) {
            padding-top: 0;
        }

        & .section-title {
            color: $mainYellow;
            font-family: eczar-bold;
            text-transform: capitalize;
            text-shadow: $textShadow;
        }

        & .products-center {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(13rem, 1fr));
            grid-template-rows: auto;
            grid-column-gap: 2rem;
            justify-items: center;
            align-items: center;
            margin: auto;

            & .img-container {
                position: relative;
                overflow: hidden;
                box-shadow: $boxShadow;

                &:hover .bag-btn {
                    transform: translateX(0);
                }
                &:hover .product-img {
                    opacity: .6;
                }

                & .product-img {
                    display: block;
                    width: 100%;
                    transition: all 0.3s ease;

                    @media screen and (min-width:500px) {
                        width: 120%;
                    }
                }

                & .bag-btn {
                    position: absolute;
                    top: 70%;
                    right: 0;
                    background: rgba($mainGreen, .6);
                    border: none;
                    color: $white;
                    font-size: $fontMd;
                    text-shadow: $textShadow;
                    box-shadow: -0.125rem -0.125rem 0.525rem $mainBlack;
                    padding: 0.5rem;
                    transition: all .5s ease;
                    transform: translateX(101%);
                    cursor: pointer;
                }
            }

            & h4 {
                color: $white;
                font-family: eczar-reguar;
                font-size: 1.5rem;
                line-height: .7;
                text-transform: capitalize;
                text-shadow: $textShadow
            }
        }
    }

    & .cart-overlay {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        transition: all .5s ease;
        background: rgba($btnColor,.5);
        z-index: 2;
        visibility: hidden;

        &.transparentBcg {
            visibility: visible;
        }

        & .cart {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            overflow: scroll;
            z-index: 3;
            background: $white;
            padding: 1.5rem;
            transition: all .5s ease;
            transform: translateX(100%);

            @include breakpoint-up(medium) {
                width: 40vw;
            }

            &.showCart {
                transform: translateX(0);
            }

            & .close-cart {
                font-size: $fontLg;
                cursor: pointer;
            }

            & h3 {
                font-family: eczar-bold;
                font-size: $fontMd;
                color: $mainBackground;
                text-transform: uppercase;
            }
            
            & .cart-content {
                & .cart-item {
                    display: grid;
                    grid-template-columns: 2fr 1fr auto;
                    grid-gap: 2rem;
                    align-items: center;
                    margin: 1rem 0;

                    & img {
                        width: 100%;
                        box-shadow: $boxShadow;
                    }

                    & > div {
                        text-align: center;

                        & h4 {
                            text-transform: capitalize;
                            
                        }
    
                        & .remove-item {
                            color: $mainRed;
                            cursor: pointer;
                            

                            & .fa-trash {
                                font-size: $fontMd;
                                text-shadow: $textShadow;
                            }

                            &:hover {
                                color: $btnColor;
                            }
                        }
                    }

                    & .fa-chevron-up, .fa-chevron-down {
                        cursor: pointer;
                        
                    }

                    & .item-amount {
                        text-align: center;
                        font-family: eczar-bold;
                    }
                }
            }

            & .cart-footer {
                margin-top: 2rem;
                text-align: center;

                & h3 {
                    text-transform: uppercase;
                    font-size: $fontMd;
                    font-family: eczar-bold;

                    & span {
                        font-size: 1.5rem;
                    }
                }

                & .clear-cart {
                    padding: .5rem;
                    background: transparent;
                    border: .1rem solid $mainBackground;
                    color: $mainBackground;
                    font-family: eczar-bold;
                    box-shadow: $boxShadow;
                    outline: none;

                    &:hover {
                        background: $mainRed;
                        color: $white;
                    }
                }
            }
        }
    }
}