.slider {
    padding: 1rem;
    position: relative;
    
    & .slider-title {
        grid-column: 1/-1;
        text-align: center;
        font-family: eczar-bold;
        color: $mainYellow;
        text-transform: uppercase;
        text-shadow: $textShadow;
    }

    & .slider-wrapper {
        grid-column: 1/-1;
        width: 90%;
        height: 90%;
        padding: 1.875rem;
        margin: 0 auto;
        background: linear-gradient(
            rgba($mainBlack, .5), 
            rgba($mainBlack,.5)), 
            url(/dist/images/guitar.jpg) center/cover no-repeat;
        border-radius: .625rem;
        z-index: 5;
        overflow: hidden;
        box-shadow: $boxShadow;

        @include breakpoint-up(medium) {
            height: 100%;
            width: 85%;
        }

        @include breakpoint-up(large) {
            width: 70%;
        }

        & .slider-content {
            overflow: hidden;
            position: relative;

            & > div {
                text-align: center;
                position: absolute;
                opacity: 0;

                &.active {
                    position: relative;
                    opacity: 1;
                }

                &.inactive {
                    opacity: 1;
                }

                @include breakpoint-up(medium) {
                    display: grid;
                    grid-template-columns: repeat(2, 1fr);
                }

                & p {
                    font-size: $fontMd;
                    font-family: eczar-regular;
                    color: $white;
                    line-height: 1.5;
                    text-shadow: $textShadow;
                    padding-bottom: 1rem;

                    @include breakpoint-up(medium) {
                        font-size: $fontMd;
                        font-family: eczar-bold;
                        width: 100%;
                        text-align: left;
                        padding-top: 2rem;
                    }
                }
            }

            & .slider-img {
                display: flex;
                align-items: center;
                text-align: left;

                & img {
                    width: 7.625rem;
                    height: 7.625rem;
                    border-radius: 50%;
                    margin: 1rem;
                    border: .2rem solid gray;
                    box-shadow: $boxShadow;

                    @include breakpoint-up(medium) {
                        width: 8.625rem;
                        height: 8.625rem;
                    }
                }

                & h4 {
                    color: $white;
                    font-size: $fontMd;
                    font-family: eczar-regular;
                    line-height: 1.2;
                    text-shadow: $textShadow;

                    small {
                        font-size: .5rem;
                        font-family: eczar-regular;
                        text-transform: uppercase;
                    }

                    @include breakpoint-up(large) {
                        font-size: 1.4rem;
                    }
                }
            }
        }
    }

    & .left-arrow {
        width: 100%;
        position: absolute;
        top: 95%;
        left: 35%;
        cursor: pointer;

        @include breakpoint-up(medium) {
            top: 60%;
            left: 2%;
        }

        @include breakpoint-up(large) {
            left: 10%;
        }
    }

    & .right-arrow {
        width: 100%;
        position: absolute;
        top: 95%;
        right: -62%;
        cursor: pointer;

        @include breakpoint-up(medium) {
            top: 60%;
            left: 96%;
        }

        @include breakpoint-up(large) {
            left: 89%;
        }
    }
}