.tabs {
    padding: 0 1rem;
    margin: 1rem 0;

    @include breakpoint-up(large) {
        margin: 2rem 0;
    }

    & .container {
        overflow: hidden;
        background: linear-gradient(
            rgba($mainBlack, .8),
            rgba($mainBlack, .8)),
            url(/dist/images/dr-gregory-house3.jpg) top center/cover no-repeat;
        box-shadow: $boxShadow;
        height: 100%;
        border-bottom: .3rem double rgba($mainBlack, .5);

        .tabs-header {
            grid-column: 1/-1;
            margin: 0 auto;
            text-align: center;
            width: 100%;
            max-width: 35rem;
            padding-bottom: .5rem;

            & h3 {
                color: $mainYellow;
                font-family: eczar-regular;
                text-transform: capitalize;
                text-shadow: $textShadow;
                margin-bottom: 0;
                line-height: 1.3;
            }

            & p {
                color: $white;
                font-family: eczar-regular;
                text-shadow: $textShadow;
                line-height: 1.4;
            }
        }
        .tabs-btn-box {
            padding: 0 1rem;
            grid-column: 1/-1;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            justify-content: center;
            align-items: center;
            text-align: center;

            @include breakpoint-up(medium) {
                margin: 0 4rem;
            }

            @include breakpoint-up(large) {
                grid-column: 3/11;
            }

            & button {
                margin-top: -1rem;
                background: transparent;
                color: $white;
                border: 0;
                outline: 0;
            }

            & #btn1, #btn2, #btn3 {
                font-size: $fontMd;
                font-family: eczar-bold;
                text-transform: uppercase;
                text-shadow: $textShadow;
                border-bottom: .3rem solid $btnColor;
                cursor: pointer;
            }

            @include breakpoint-up(medium) {
                font-size: 1.3rem;
            }

            & #btn3 {
                color: $mainYellow;
                border-bottom-color: $white;

                @include breakpoint-up(medium) {
                    color: $white;
                    border-bottom-color: $btnColor;
                }
            }

            & #btn1 {
                @include breakpoint-up(medium) {
                    color: $mainYellow;
                    border-bottom-color: $white;
                }
            }
        }

        .tabs-content {
            padding: 0 1rem;
            grid-column: 1/-1;
            grid-row: 3;
            height: 70vh;
            transform: translateX(100%);
            transition: transform .5s ease-in-out;

            @include breakpoint-up(medium) {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-gap: 1rem;
                align-items: center;
                justify-content: center;
                text-align: center;
            }

            &#content1 {
                @include breakpoint-up(medium) {
                    transform: translateX(0%);
                }
            }

            &#content3 {
                transform: translateX(0%);

                @include breakpoint-up(medium) {
                    transform: translateX(100%);
                }
            }

            & .content-left {
                @include breakpoint-up(medium) {
                    text-align: left;
                    padding-left: 2rem;
                    transform: translateY(-10%);
                }

                @include breakpoint-up(large) {
                    transform: translateY(-20%);
                }

                & h3 {
                    color: $mainYellow;
                    font-family: eczar-regular;
                    text-shadow: $textShadow;
                    text-align: center;
                    margin: 0;
                    line-height: 1.3;

                    @include breakpoint-up(medium) {
                        text-align: start;
                        width: 23rem;
                        font-size: $fontLg;
                        line-height: 1.3;
                    }

                    @include breakpoint-up(large) {
                        width: 100%;
                    }
                }

                & p {
                    font-size: $fontSm;
                    font-family: eczar-regular;
                    color: $white;
                    text-shadow: $textShadow;
                    line-height: 1.5;
                    margin-bottom: 1rem;
                    text-align: center;

                    @include breakpoint-up(medium) {
                        font-size: $fontMd;
                        text-align: left;
                    }
                }
            }

            &#content3 .content-left {
                @include breakpoint-up(large) {
                  transform: translateY(-55%);
                }
              }

            & .content-right {
                display: flex;
                justify-content: flex-end;

                & img {
                    display: block;
                    width: 60%;
                    text-align: right;

                    @include breakpoint-up(medium) {
                        width: 80%;
                    }

                    @include breakpoint-up(large) {
                        width: 90%;
                    }
                }
            }
        }
    }
}