.team-members {
    padding: 1rem;
    margin: 0 1rem;

    & .team-title {
        grid-column: 1/-1;
        font-family: eczar-regular;
        color: $mainYellow;
        text-transform: capitalize;
        text-shadow: $textShadow;
       
        &:hover .title-underline {
            width: 15rem;
        }
    }

    & .title-underline {
        margin-top: -1.5rem;
        width: 3.125rem;
        border: .3rem solid $white;
        box-shadow: $boxShadow;
        transition: all .5s ease-in-out;
    }

    & .team-center {
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(17rem, 1fr));
        grid-gap: 1rem;
        justify-content: center;
        align-items: center;

        & .team-member {
            background: 
            linear-gradient(
                rgba($mainBackground, .3), 
                rgba($mainBackground, .3)), 
                url(/dist/images/matrix.jpg) center/cover no-repeat;
            padding: 1rem 2rem;
            width: 100%;
            height: auto;
            border-radius: .5rem;
            box-shadow: $boxShadow;

            & img {
                display: block;
                margin: 0 auto;
                max-width: 16rem;
                max-height: 11rem;
                border: .1rem solid $white;
                transition: transform 1.5s ease-in-out;
                cursor: pointer;

                &:hover {
                    transform: rotateY(-180deg);
                }
            }

            & .footer-info {
                line-height: .7;
                text-align: center;

                & h3 {
                    font-family: eczar-regular;
                    font-size: $fontLg;
                    color: $mainYellow;
                    text-transform: capitalize;
                    text-shadow: $textShadow;

                    & small {
                        font-size: $fontMd;
                        text-transform: uppercase;
                    }
                }

                & .social-icons {
                    display: flex;
                    justify-content: space-around;

                    & .fa {
                        color: $btnColor;
                        font-size: 1.5rem;
                        text-shadow: $textShadow;
                        transition: transform .5s ease, color .5s ease;

                        &:hover {
                            transform: translateY(-50%);
                            color: $mainYellow;
                        }
                    }
                }
            }
        }
    }
}