.partners {
    padding: 3rem 0;

    & h2 {
        grid-column: 1/-1;
        text-align: center;
        font-family: eczar-regular;
        text-transform: uppercase;
        color: $mainYellow;
        text-shadow: $textShadow;
        padding-bottom: 1rem;
    }

    & .partners-logos {
       height: 100%;
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: 5rem;
        grid-row-gap: 5rem;
        justify-content: center;
        justify-items: center;
        margin-bottom: 3rem;

        @include breakpoint-up(medium) {
            grid-template-columns: repeat(5, 1fr);
            justify-content: start;
            margin: 1rem;
        }

        & img {
            display: block;
            width: 150%;
            height: auto;
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
            filter: grayscale(100%);
            transition: all .3s ease-in-out;
            align-self: center;

            @media screen and (min-width: 31.25rem) {
                max-width: 50vw;
            }

            &:last-child {
                grid-column: 1/-1;
                width: 100%;

                @include breakpoint-up(medium) {
                    grid-column: 5;
                    width: 130%;
                }
            }

            &:hover {
                -webkit-filter: none;
                -moz-filter: none;
                filter: none;
                opacity: 1.25;
                transform: skew(-2deg, 2deg) scale(1.1);
            }
        }
    }
}